/* Elements */
header {
  padding: 0;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  height: 60px;
  width: 100%;
}

header label {
  font-size: 2.5em;
  z-index: 9999;
}

header .logo {
  height: 35px;
  margin-left: 20px;
}

header .logo img {
  margin-left: 0;
}

header label {
  margin-right: 20px;
}

header button:active:focus {
  outline: none;
}

header .sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

header input {
  display: none;
}

header input[type="checkbox"]:checked ~ nav {
  display: flex;
  justify-content: flex-start;
  padding-top: 45px;
}

header input[type="checkbox"]:checked ~ label {
  color: white;
}

/* Menu navigation */
nav {
  position: absolute;
  display: none;
  flex-direction: column;
  justify-content: center;
  z-index: 900;
  transition: transform 0.3s ease;
  top: 0;
  right: 0;
  height: 100vh;
  min-height: 100vh;
  width: 320px;
  background-color: #363636;
}

nav .menu_navigation {
  padding-inline-start: 0px;
  display: flex;
  flex-direction: column;
  list-style: none;
  height: 350px;
  justify-content: space-between;
}

nav .menu_navigation li {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

nav .menu_navigation li span {
  margin-right: 10px;
}

nav .menu_navigation .li-dropdown {
  display: flex;
  flex-direction: column;
  align-items: center;
}

nav .menu_navigation .li-dropdown span {
  color: white;
  font-size: 22px;
  margin-left: -48px;
}

nav .menu_navigation .li-dropdown ul {
  width: 100%;
  padding-inline-start: 35px;
}

nav .menu_navigation .li-dropdown ul li a {
  font-size: 18px;
}

nav .menu_navigation a {
  border-bottom: 1px solid #363636;
  text-decoration: none;
  display: block;
  width: 65%;
  font-size: 22px;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: white;
  text-align: left;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

nav .menu_navigation a:hover,
nav .menu_navigation a:focus,
nav .menu_navigation a:active {
  color: #c12c3e;
}

.header--pageCategorie {
  flex-direction: column;
}

.header--pageCategorie .firstLine-Header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
}

.header--pageCategorie .firstLine-Header .logo {
  max-width: 100px;
}

.header--pageCategorie .firstLine-Header .li-dropdown span {
  color: white !important;
}

.header--pageCategorie .firstLine-Header .li-dropdown li:last-child:hover {
  background-color: #d93e51;
}

