.header--redPage {
  flex-direction: column;
}

.header--redPage .firstLine-Header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
}

.header--redPage .firstLine-Header .logo {
  max-width: 100px;
}

.header--redPage .firstLine-Header .li-dropdown span {
  color: white !important;
}

.header--redPage .firstLine-Header .li-dropdown li:last-child:hover {
  background-color: #d93e51;
}

@media (min-width: 768px) {
  header {
    margin-bottom: 40px;
  }
  header input[type="checkbox"]:checked ~ nav {
    display: flex;
  }
  header label {
    display: none;
  }
  nav {
    display: block;
    margin-right: 20px;
    width: auto;
    background-color: transparent;
    height: 60px;
    min-height: 60px;
  }
  nav .menu_navigation {
    flex-direction: row;
    justify-content: space-around;
    height: inherit;
    width: 100%;
  }
  nav .menu_navigation a {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    border-bottom: none;
    color: #363636;
    font-weight: 500;
  }
  nav .menu_navigation span {
    display: flex;
    color: #363636 !important;
  }
  nav .menu_navigation span svg {
    display: none;
  }
  nav .menu_navigation li {
    padding: 0px 20px;
  }
  nav .menu_navigation li span {
    margin-right: 0px;
  }
  nav .menu_navigation .li-dropdown {
    display: flex;
    transition-duration: 0.5s;
    font-size: 18px;
    font-weight: 500;
    position: relative;
  }
  nav .menu_navigation .li-dropdown ul {
    width: auto;
  }
  nav .menu_navigation .li-dropdown span {
    font-size: 18px;
    margin-left: 0px;
  }
  nav .menu_navigation .li-dropdown:hover {
    color: #c12c3e;
  }
  nav .menu_navigation .dropdown {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    transition: all 0.5s ease;
    color: white;
    top: 60px;
    padding-left: 0px !important;
  }
  nav .menu_navigation .dropdown li {
    background-color: #c12c3e;
    clear: both;
    font-size: 15px;
    align-items: flex-start;
    margin: 0px;
    white-space: nowrap;
    justify-content: flex-start;
    align-items: center;
    height: 40px;
  }
  nav .menu_navigation .dropdown li a {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 15px !important;
    height: 100%;
    width: 100%;
    padding: 10px;
  }
  nav .menu_navigation .dropdown li a:hover {
    color: white;
  }
  nav .menu_navigation .dropdown li:hover {
    background-color: #d93e51;
  }
  nav .menu_navigation .dropdown li:last-child a {
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 15px;
    padding: 10px 20px;
    border-radius: 0px;
  }
  nav .menu_navigation .li-dropdown:hover > ul,
  nav .menu_navigation .li-dropdown ul:hover {
    visibility: visible;
    opacity: 1;
    display: block;
  }
  nav .menu_navigation li:last-child {
    display: flex;
    align-items: center;
  }
  nav .menu_navigation li:last-child a {
    display: flex;
    align-items: center;
    height: 30px;
    padding: 2px 15px;
    color: white;
    border-radius: 25px;
    border: 0px;
    font-weight: 600;
    font-size: 15px;
  }
  nav .menu_navigation > li:last-child a {
    color: white;
    background-color: #c12c3e;
  }
  .header--redPage {
    height: auto;
    min-height: 200px;
    flex-direction: column;
    background: #c12c3e;
  }
  .header--redPage .logo img {
    content: url("../ressources/img/logos/logo-white.svg");
  }
  .header--redPage .firstLine-Header {
    width: 100%;
  }
  .header--redPage nav .menu_navigation li a {
    color: white;
  }
  .header--redPage nav .menu_navigation > li:last-child a {
    color: #c12c3e;
    background-color: white;
  }
  .header--redPage .separateur-outToWhite {
    display: block;
  }
}

