.introduction {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.section-contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
}

.section-contact .contain-form, .section-contact .illustration {
  width: 100%;
  max-width: 450px;
}

.section-contact .illustration, .section-contact .introduction, .section-contact .formulaire {
  margin-bottom: 15px;
}

.section-contact .illustration {
  order: -1;
  height: 130px;
  display: flex;
  justify-content: center;
}

.section-contact .illustration img {
  height: 100%;
}

.section-contact .introduction {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-contact .introduction h1, .section-contact .introduction span {
  font-size: 18px;
}

.section-contact .introduction span img {
  height: 22px;
}

.section-contact .formulaire {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-contact .formulaire div:nth-child(1) {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-contact .formulaire input[type="text"], .section-contact .formulaire input[type="email"], .section-contact .formulaire textarea {
  outline: none;
  width: 100%;
  max-width: 450px;
  -webkit-appearance: none;
  padding: 10px;
  background-color: #E6E3F4;
  border: 0px;
  margin: 10px 0px;
  padding: 20px;
}

.section-contact .formulaire input[type="submit"] {
  width: auto;
  min-width: 135px;
  margin: 0 auto;
}

.section-contact .formulaire input {
  height: 40px;
}

.section-contact .formulaire .text {
  border-radius: 15px;
}

.section-contact .formulaire .textarea {
  border-radius: 10px;
}

@media (min-width: 768px) {
  .section-contact {
    flex-direction: row;
    justify-content: space-around;
  }
  .section-contact .illustration {
    order: 1;
    max-width: 300px;
    height: 160px;
    width: 100%;
    max-height: 200px;
  }
}

/* Section Hello */
.sectionHello h2 {
  font-size: 18px;
  color: #363636;
}

.sectionHello h1 {
  font-size: 22px;
  font-weight: 400;
}

.sectionHello h1 img {
  height: 22px;
}

.sectionHello .avatar {
  display: none;
}

.sectionHello .description {
  display: none;
}

.sectionHello .btn_contact {
  display: none;
}

.sectionHello button {
  display: none;
}

.sectionHello a {
  display: none;
}

/* Section Article */
.sectionArticles {
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.sectionArticles h2 {
  margin-top: 15px;
}

.sectionArticles .article {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.sectionArticles .article .imgArticle {
  height: 30vw;
  min-height: 95px;
  max-height: 135px;
  width: 30vw;
  min-width: 95px;
  max-width: 180px;
}

.sectionArticles .article .imgArticle img {
  height: 100%;
  width: 100%;
  border-radius: 10px;
  object-fit: cover;
  object-position: center;
}

.sectionArticles .article .metaArticle {
  margin-top: 10px;
  max-width: 550px;
  margin-bottom: 20px;
}

.sectionArticles .article .metaArticle h3 a {
  font-size: 18px;
  font-weight: 700;
  color: #fbfbfd;
}

.sectionArticles .article .metaArticle p {
  font-size: 14px;
  color: #fbfbfd;
  font-weight: 400;
  margin-bottom: 0px;
}

.sectionArticles .article .metaArticle span {
  font-size: 13px;
  color: #fbfbfd;
  font-weight: 500;
}

.sectionArticles .article a {
  white-space: nowrap;
}

/* Section Clients */
.sectionClients {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sectionClients h3 {
  font-size: 22px;
  font-weight: 500;
}

.sectionClients h3 img {
  height: 22px;
}

.sectionClients div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.sectionClients .imgClient {
  margin: 20px;
  height: 45px;
}

.sectionClients .imgClient img {
  height: 100%;
}

@media (min-width: 768px) {
  .sectionHello {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: auto;
    width: 100%;
    align-items: center;
  }
  .sectionHello h1 {
    display: inline-block;
    max-width: 500px;
    text-align: center;
    letter-spacing: 2.4px;
    font-weight: 600;
    font-size: 22px;
    margin-bottom: 15px;
  }
  .sectionHello h2 {
    font-size: 20px;
    margin-bottom: 15px;
  }
  .sectionHello .avatar {
    display: block;
    width: 80px;
    height: 80px;
    margin-bottom: 15px;
  }
  .sectionHello .avatar img {
    height: 100%;
    width: 100%;
    border-radius: 200px;
    object-position: -3px 0px;
    object-fit: cover;
  }
  .sectionHello .description {
    display: block;
    width: 75%;
    background-color: #fcfcfc;
  }
  .sectionHello .description p {
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    letter-spacing: 0.18px;
  }
  .sectionHello a {
    display: block;
  }
  .sectionParcours {
    height: auto;
  }
  .sectionParcours .parcours {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    height: auto;
  }
  .sectionArticles .article .metaArticle {
    margin-left: 20px;
  }
  .sectionArticles .article .metaArticle h3 {
    font-size: 20px;
  }
  .sectionArticles .article .metaArticle p {
    font-size: 16px;
  }
  .sectionArticles .article .metaArticle span {
    font-size: 13px;
  }
  .sectionClients h3 {
    font-size: 22px;
  }
}

.sectionInfoPage--pageCategorie {
  margin-bottom: 50px;
}

.sectionInfoPage--pageCategorie .infoPage h1 {
  font-size: 22px;
  font-weight: 500;
}

.sectionInfoPage--pageCategorie .infoPage p {
  font-size: 14px;
